.banner{
	background: url("https://cdn57.androidauthority.net/wp-content/uploads/2021/03/airbnb-example-home-scaled.jpg") center center;
	height: 50vh;
	position: relative;
}

.banner__info {
	background-color: black;
	color: white;
	padding-top: 25vh;
	padding-left: 50px;
	padding-right: 50px;
	padding-bottom: 40px;
	width: 300px;
	height: 19vh;
}

.banner__info > button {
	background-color: #ff7779;
	color: white;
	text-transform: inherit;
	margin-top: 15px;
	font-weight: 600;
}

.banner__info > button:hover{
	background-color: white;
	color: #ff7779;
}

.banner__info > h5 {
	margin-top: 10px;
}

.banner__searchButton{
	background-color: white !important;
	font-weight: 900 !important;
	text-transform: inherit !important;
	color: #ff7779 !important;
}

.banner__search{
	display: flex;
	flex-direction: column;
}